.PostItNote {
    display: flex;
    flex-flow: column nowrap;
    justify-items: center;
    background-color: rgb(242, 243, 169);
    margin: 2%;
    width: 22rem;
    height: 26rem;
    font-size: 1rem;
    text-align: justify;
    border-radius: 5px;
}
#red{
    background-color: rgb(231, 133, 133);
}
#green {
    background-color: rgb(147, 214, 153);
}
#purple{
    background-color: rgb(214, 179, 236);
}
#orange{
    background-color: rgb(255, 206, 151);
}

.PostItTitle {
    display: flex;
    margin-right: auto;
    padding-left: 3%;
    margin: 3%;
    align-self: stretch;
    height: fit-content;
    font-size: 1.6rem;
    background: none;
    border: none;
    resize: none;
}

.TitleDiv {
    height: fit-content;
    padding-left: 0%;
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 100%;
}

.dateAndButtons {
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.1);
    width: 100%;
    height: fit-content;
    border-radius: 5px 5px 0px 0px;
}

.dateAndButtons span {
    margin: 3%;
    margin-right: auto;
}

.dateAndButtons Button {
    background-color: Transparent;
    background-repeat:no-repeat;
    border: none;
    display: block;
    padding: 0px;
    margin: 0.3rem;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    font-weight: 750;
}

.dateAndButtons img { 
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 3rem;
    height: 3rem;
}

.PostItParagraph {
    text-align:justify;
    margin: 3%;
    height: 100%;
    font-size: 1rem;
    background: none;
    border: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    resize: none;
}
