
.Header {
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  background-image: linear-gradient(rgb(231, 216, 133), rgb(244, 238, 255));
  width: 100%;
}

#recycleBin{
  background-image: linear-gradient(rgb(7, 0, 39), rgb(244, 238, 255));
  color: white;
}

p{
  color: rgb(7, 0, 39);
  font-size: 1.25rem;
  font-weight: bold;
}
.HeaderText {
  display: flex;
  flex-flow: column;
  justify-content: center;
  margin: 2rem;
  margin-right: 3rem;
}

.HeaderText h1 {
  font-size: 2.5rem;
}

.HeaderText p,  .headerButtons button{
  font-size: 1.5rem;
  margin: 0.5rem;
}

.headerButtons {
  display: flex;
  flex-flow: row;
  margin: 1rem;
  width: 100%;
  justify-content: space-evenly;

}

.headerButtons Button {
  background-color: Transparent;
  /*background-repeat:no-repeat;*/
  border: none;
  padding: 0px;
  margin: 0.3rem;
  border-radius: 50%;
  font-weight: 750;
  height: 8rem;
}

#recycleBin .headerButtons button {
  color: white;
  width: 10rem;
  border: ridge;
  border-radius: 10px;
}

img {
  height: 8rem;
}

@media screen and (min-width: 768px){
  .Header {
    flex-flow: row;
  }
  .headerButtons {
    min-width: none;
    width: auto;
    align-items: center;
    justify-content: space-between;
  }
  .headerButtons button {
    height: 100%;
  }
}